
import Vue from 'vue'
export default Vue.extend({
  name: 'Thread',
  props: {
    initial_username: {
      type: String,
    },
    initial_password: {
      type: String,
    }
  },
  data() {
    return {
      username: '',
      password: '',
      confirm_password: '',
      password_show: false,
      confirm_password_show: false,
    }
  },
  methods: {
    user_valid(v: string) {
      return v != 'admin' || "This username is already used!"
    },
    required(v: string) {
      return !!v || 'Required'
    },
    match(v: string) {
      return (v == this.password) || "The passwords do not match!"
    },
    on_register() {
      if ((this.$refs.form as Vue & { validate: () => boolean }).validate()) {
        // database update
        const id = "testID"
        this.$router.push({name: 'users', params: {user_id: id}})
      }
    },
  },
  computed: {

  },
  created() {
    this.username = this.initial_username ?? ''
    this.password = this.initial_password ?? ''
  },
})
