
import Vue from 'vue'

import Post from '@/models/post'

export default Vue.extend({
  name: 'PostComponent',
  props: {
      post: {
        type: Post,
        required: true
      }
  },
  methods: {
  },
  computed: {
      processed_text() {
        // todo: process markdown and spoilers here

        return (this.post as Post).content || ''
      }
  },
  data() {
      return {
          not_done: true
      }
  },
  mounted() {
    if ((this as any).not_done) {
      (this as any).not_done = false;

      Array.from(document.querySelectorAll('.spoiler')).forEach(spoiler => {
        spoiler.addEventListener("click", _ => {
          spoiler.classList.add('opened-spoiler')
        })
      }, false)
    }
  },
})
